.label{
  display: block;
  font-size: larger;
  color: white;
  padding: 5px;
  }
  
.input{
  font-size: larger;
  padding: 5px;
  margin: 2px;
}
.btn{
  color: white;
  background-color: red;
  border-radius: 5px;
  font-size: larger;
  display: block;
  padding: 5px;
  margin: 10px auto;
}


.main {
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 0 20px
      rgba(0, 0, 0, 0.2);
  padding: 20px;
  width: 300px;
  margin: auto;
}

.main h2 {
  color: #4caf50;
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
  color: #555;
  font-weight: bold;
}

input[type="text"],
input[type="email"],
input[type="password"],
select {
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.RegisterButton{
  padding: 15px;
  border-radius: 10px;
  border: none;
  background-color: #282C34;
  color: white;
  cursor: pointer;
  width: 100%;
  font-size: 16px;
}
.noaccount{
  padding: 15px;
  border-radius: 10px;
  border: none;
  background-color: #282C34;
  color: white;
  cursor: pointer;
  width: 20%;
  font-size: 16px;
}
.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: 60%;
  margin: auto; 
  display: flex;
  justify-content: center;
  background-color: #080127;
  margin-top: 5%;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(255, 255, 255, 0.2);
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (prefers-reduced-motion: no-preference) {
  .spin {
    animation: spin infinite 0.7s linear;
  }
}